import React, { useState, useContext, useEffect } from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";

import ListItem from "@material-ui/core/ListItem";
import InputBase from "@material-ui/core/InputBase";
import List from "@material-ui/core/List";

import MultipleDestinations from "@cvccorp-components/icons/dist/MultipleDestinations";
import Clock from "@cvccorp-components/icons/dist/Clock";
import ArrowLeft from "@cvccorp-components/icons/dist/ArrowLeft";
import ErrorIcon from "@cvccorp-components/icons/dist/ErrorIcon";

import Typography from "../../Typography";
import Box from "../../Box";
import TextField from "../../TextField";
import Overlay from "../../Overlay";
import Animate from "../../Animate";
import { styles } from "../styles";
import { BrandContext } from "../../BrandContext";

const mergeClasses = (classes) => classes.join(" ");

const tourClasses = theme => ({
    fieldContainer: {
        position: "relative",
    },
    destinationscontainer: {
        position: "absolute",
        top: "50%",
        left: "0",
        overflowY: "auto",
        width: "100%",
        display: "inline-flex",
        transform: "translateY(-50%)",
        whiteSpace: "nowrap",
    },
    destinationField: {
        margin: "0 4px",
        padding: "3px 10px 0 10px",
        fontSize: "14px",
        width: "100%",
    },
    destinationFieldTransparent: {
        color: theme.palette.text.primary,
    },
    destinationInput: {
        color: "transparent",
        height: "42px",
        border: `1px solid ${theme.palette.greyscale[3]}`,
    },
    closeButton: {
        width: "24px",
        marginLeft: "8px",
        marginRight: "-4px",
        color: theme.palette.primary.darkHover,
        float: "right",
    },
    destinationInputLabel: {
        paddingLeft: "0",
        color: theme.palette.text.primary,
        fontSize: "14px",
    }
});

const PesqincTourFilter = ({
    classes,
    onChange,
    onFocus,
    onClose,
    onSelect,
    value,
    selectedIndex,
    transparent,
    search,
    isMobile,
    error,
    errorMsg,
    history,
    mostSearched,
    initialDestination,
}) => {
    const [index, setIndex] = useState(selectedIndex || 0);
    const [searching, setSearching] = useState(false);
    const [selectedItem, setSelectedItem] = useState(initialDestination || null);
    const [loaded, setLoaded] = useState(false);
    const { theme: brandTheme } = useContext(BrandContext);

    useEffect(() => {
        setLoaded(true);
    }, []);

    useEffect(() => {
        if (initialDestination) {
            handleSelect(initialDestination);
        }
    }, [initialDestination]);

    const removeDestination = () => {
        setSelectedItem(null);
        onSelect(null);
    };

    const handleSelect = (item) => {
        setSearching(false);
        setSelectedItem(item);
        onSelect(item);
    };

    const handleKeyUp = e => {
        if (search && search.length > 0) {
            if (e.keyCode === 40) setIndex((index + 1) % search.length);
            else if (e.keyCode === 38)
                setIndex((index - 1 + search.length) % search.length);
            else if (e.keyCode === 13 && searching) {
                onSelect(selectedItem);
                setSearching(false);
            }
        }
    };

    const handleChange = e => {
        if (onChange) onChange(e);
    };

    const handleFocus = e => {
        setSearching(true);
        if (onFocus) onFocus(e);
    };

    const handleClose = () => {
        if (window !== undefined && isMobile) {
            window.scrollTo(0, 0);
        }
        if (!selectedItem || value !== selectedItem.name) {
            setSelectedItem(null);
        }

        setSearching(false);
        if (onClose) onClose(selectedItem);
    };

    const [isOpenAnimation, setOpenAnimation] = useState(false);

    const onOpenAnimate = () => {
        setOpenAnimation(true);
        document.getElementById("pesqinc-input").focus();
    };
    const onCloseAnimate = () => {
        setOpenAnimation(false);
    };

    const renderDestinationsContainer = () => {
        if (!selectedItem) {
            return;
        }
        const renderDestination = (destination) => {
            return (
                <div className={mergeClasses([classes.destinationField, classes.destinationFieldTransparent])}>
                    <Typography>{destination.description}</Typography>
                    <span className={classes.closeButton} onClick={removeDestination}>
                        <ErrorIcon />
                    </span>
                </div>
            );
        };
        return (
            <div className={classes.destinationscontainer}>
                {renderDestination(selectedItem)}
            </div>
        );
    };

    const textLabel = () => selectedItem ? "" : "Digite seu destino ou cidade";

    return (
        <div className={classes.container}>
            {loaded && (
                <div className={mergeClasses([classes.fieldContainer, "fieldContainer"])}>
                    <TextField
                        label={textLabel()}
                        size="medium"
                        transparent={transparent}
                        onClick={handleFocus}
                        onChange={handleChange}
                        onKeyUp={handleKeyUp}
                        inputClassName={mergeClasses([classes.input, classes.destinationInput])}
                        labelClassName={mergeClasses([classes.input, classes.destinationInputLabel])}
                        error={error}
                        errorMsg={errorMsg}
                        disabled
                        noBorder
                    />
                    {renderDestinationsContainer()}
                </div>
            )}

            <Animate
                isOpen={searching}
                onOpen={onOpenAnimate}
                onClose={onCloseAnimate}
            >
                {handleClose && <Overlay onClick={handleClose} />}

                <Box
                    className={`${classes.root} ${!isOpenAnimation && classes.closed}`}
                >
                    <div className={classes.internalInput}>
                        {isMobile ? (
                            <a onClick={handleClose}>
                                <ArrowLeft />
                            </a>
                        ) : <MultipleDestinations noStroke size="medium" />}
                        <InputBase
                            placeholder="Para onde vai?"
                            onFocus={handleFocus}
                            onChange={handleChange}
                            onKeyUp={e => handleKeyUp(e, value)}
                            className={classes.flexGrow}
                            id="pesqinc-input"
                            autoComplete="off"
                        />
                    </div>
                    <div className={classes.boxResult}>
                        {!history && (!search || search.length === 0) && !mostSearched && (
                            <Typography
                                type="captionsemibold"
                                className={classes.listItemTitle}
                            >
                                Continue digitando…
                            </Typography>
                        )}
                        <List component="nav" className={classes.list}>
                            {search && search.length > 0 && (
                                <li className={classes.listItemTitle}>
                                    <Typography type="captionsemibold">
                                        Você está buscando por…
                                    </Typography>
                                </li>
                            )}
                            <div
                                className={`${!isMobile && classes.heightLimit} ${classes.scrolling}`}
                            >
                                {search &&
                                    search.map((item, i) => (
                                        <ListItem
                                            key={i}
                                            className={classes.listItem}
                                            button
                                            selected={i === index}
                                            onClick={() => handleSelect(item)}
                                        >
                                            {item.type === "location" && <MultipleDestinations size="small" noStroke />}
                                            <span className={classes.content}>
                                                <Typography
                                                    className={classes.name}
                                                    type="body2semibold"
                                                >
                                                    {item.name}
                                                </Typography>
                                                <Typography
                                                    className={classes.address}
                                                    type="body2regular"
                                                >
                                                    {" - "}
                                                    {item.address}
                                                </Typography>
                                            </span>
                                        </ListItem>
                                    ))}
                            </div>
                            {history && !(search && search.length > 0) && (
                                <li className={classes.listItemTitle}>
                                    <Typography type="captionsemibold">Últimas buscas</Typography>
                                </li>
                            )}
                            {history &&
                                !(search && search.length > 0) &&
                                history.map((item, i) => (
                                    <ListItem
                                        key={i}
                                        className={classes.listItem}
                                        button
                                        onClick={() => handleSelect(item)}
                                    >
                                        <Clock size="small" />
                                        <span className={classes.content}>
                                            <Typography className={classes.name} type="body2semibold">
                                                {item.name}
                                            </Typography>
                                        </span>
                                    </ListItem>
                                ))}
                            {mostSearched && !(search && search.length > 0) && (
                                <li className={classes.listItemTitle}>
                                    <Typography type="captionsemibold">
                                        Destinos mais buscados
                                    </Typography>
                                </li>
                            )}
                            {mostSearched &&
                                !(search && search.length > 0) &&
                                mostSearched.map((item, i) => (
                                    <ListItem
                                        key={i}
                                        className={classes.listItem}
                                        button
                                        onClick={() => handleSelect(item)}
                                    >
                                        {(item.type === "location" || !item.type) && (
                                            <MultipleDestinations size="small" noStroke />
                                        )}
                                        <span className={classes.content}>
                                            <Typography className={classes.name} type="body2semibold">
                                                {item.description || item.name}
                                            </Typography>
                                        </span>
                                    </ListItem>
                                ))}
                        </List>
                    </div>
                </Box>
            </Animate>
        </div>
    );
};

PesqincTourFilter.propTypes = {
    classes: PropTypes.object.isRequired,
    search: PropTypes.array,
    history: PropTypes.array,
    mostSearched: PropTypes.array,
    onChange: PropTypes.func,
    onFocus: PropTypes.func,
    onClose: PropTypes.func,
    onSelect: PropTypes.func,
    value: PropTypes.array,
    selectedIndex: PropTypes.number,
    transparent: PropTypes.bool,
    isMobile: PropTypes.bool,
    error: PropTypes.bool,
    errorMsg: PropTypes.string,
    initialDestination: PropTypes.array,
};

const mergeStyles = theme => {
    return {
        ...styles(theme),
        ...tourClasses(theme),
    };
};
export default withStyles(mergeStyles, { withTheme: true })(PesqincTourFilter);
